				file.url = `${self.qiniuaddr}/${res.key}`;
				var form = Qs.stringify({
					uuid: self.form.uuid,
					command: self.form.command,
					name: self.form.name,
					image_type: self.form.image_type,
					image_content: file.url,
					pass: self.form.pass
				})
